import React from "react";
import "./InfoCardLong.scss";
import PersonalInfo from "./PersonalInfo/PersonalInfo";
import ExitDummy from "../../ExitDummy/ExitDummy";
import img1 from "../../Slider/Images/5.jpg";

export default props => {
  if (!props.isOn) {
    return <div />;
  }
  const groupMembers = require("./groupMembers.js");
  const peopleToDisplay = groupMembers.default.map((member, i) => (
    <PersonalInfo key={i} src={member.src} name={member.name} />
  ));
  return (
    <div>
      <div className="page-info-extended" onClick={e => e.stopPropagation()}>
        <ExitDummy exit={props.exit} />
        <h2>O nas</h2>
        <div className="page-info-images">
          <img src={img1} alt="" />
        </div>

        {/* <div className="page-info-flex-container"> */}
        <div className="page-info-text">
          <p>
            Treblers to warszawski zesp taca irlandzkiego zaoony w 2012
            roku, kiedy to tancerze znanych i cenionych warszawskich zespow,
            ktrych korzenie sigaj 1991 roku, poczyli siy i zaczli razem
            wsptworzy choreografie. Nazwa zespou pochodzi od podstawowego
            elementu tanecznego w stepie - "treble".
          </p>
          <p>
            W naszym repertuarze znajduj si zjawiskowe tace w tradycyjnych
            irlandzkich baletkach oraz uwielbiany przez publiczno porywajcy
            step irlandzki. Wrd naszych tancerzy s wielokrotni mistrzowie
            Europy. Treblers to jedyny zesp w Polscez tak utytuowanymi
            artystami, prezentujcymi najwyszy midzynarodowy poziom taca
            irlandzkiego.
          </p>

          <p>
            Fragmenty naszych pokazw mona zobaczy na{" "}
            <a
              href="http://www.youtube.com/user/treblerspl"
              target="_blank"
              rel="noopener noreferrer"
            >
              naszym kanale youtube
            </a>
            .
          </p>
        </div>
        {/* <div className="page-info-images">
            <img src={img1} alt=""/>
          </div> */}
        {/* </div> */}

        <p>
          Na koncie mamy wystpy na rnego rodzaju festiwalach muzycznych,
          eventach tematycznych oraz imprezach integracyjnych, zarwno solowo
          jak i przy akompaniamencie muzyki najlepszych polskich zespow
          grajcych muzyk irlandzk. Taczylimy na festiwalach folkowych
          (m.in. na Festiwalu Kultury Celtyckiej w Dowspudzie i Festiwalu Muzyki
          Celtyckiej Zamek), na deskach teatrw i filharmonii, oraz w
          programach telewizyjnych m.in. "Taniec z Gwiazdami", Jaka to
          melodia, Got To Dance oraz World of Dance. Wystpowalimy u boku
          wielu artystw estradowych, m.in. Urszuli Dudziak, Roberta
          Kasprzyckiego, Muka Staszczyka, Haliny Mlynkovej oraz Aleksandra
          Rybaka.
        </p>
        <p>
          W latach 2013-2017 razem z muzykami grupy Carrantuohill
          wsptworzylimy spektakl{" "}
          <strong>Touch of Ireland  the Storm</strong>. Naszym najnowszym
          projektem taneczno-muzycznym jest widowisko irlandzkie{" "}
          <strong>Feel the Reel</strong> wsptworzone razem z muzykami z
          zespou Taorluath.
        </p>

        <p>
          Cay czas rozwijamy swoje umiejtnoci biorc udzia w
          midzynarodowych zawodach taca irlandzkiego, uczestniczc w
          warsztatach prowadzonych przez mistrzw taca irlandzkiego, midzy
          innymi Kevina McCormacka (10-krotnego mistrza wiata i solisty
          Riverdance), Lisy Delaney-Galal (nauczycielki taca irlandzkiego) czy
          Joela Hanny (choreografa i tancerza Dancing on Dangeous Ground).
          Wsppracujemy take z najlepszymi w Europie i na wiecie tancerzami
          taca irlandzkiego.
        </p>
        <h2 style={{ paddingTop: "30px" }}>Tancerze Treblers</h2>
        <div
          className="personal-info-wrapper"
          style={{
            display: "flex",
            flexWrap: "wrap",
            justifyContent: "space-around",
            alignItems: "center"
          }}
        >
          {peopleToDisplay}
        </div>
        <p>
          W poprzednich latach taczyli z nami rwnie: Ela Wojakowska,
          Agnieszka Bujak, Julia Konopka-onierczuk, Ola Sabut, Milena
          Rogowska, Dorota Kucharek, Ola Kauny, Eliza Andrzejewska, Kateryna
          Derkachova, Karolina Lipiska, Natalia Aduszkiewicz, Ola Borowska,
          Asia Kierzkowska, Magda Niepieko, Mateusz Wjcik, Ania Skupie, Ania
          Brako, Edyta Jakubowska, Magda Lempert. Dzikujemy!
        </p>
      </div>
    </div>
  );
};
