import React from "react";
import "./OfferLong.scss";
import img1 from "../../Gallery/Images/20.jpg";
import img2 from "../../Gallery/Images/6.jpg";
import img3 from "./1.jpg";
import ExitDummy from "../../ExitDummy/ExitDummy";

export default ({ exit }) => {
  return (
    <div className="page-info-extended" onClick={e => e.stopPropagation()}>
      <ExitDummy exit={exit} />
      <h2>Oferta</h2>
      <div className="page-info-flex-container">
        <div className="page-info-text">
          <p>
            Oferujemy profesjonaln opraw artystyczn imprez do muzyki zarwno
            odtwarzanej jak i granej na ywo. Poza pokazami tanecznymi oferujemy
            krtkie warsztaty taneczne dopasowane do grona odbiorcw.
          </p>
          <ul className="offer-types">
            <li>
              <strong>POKAZY TANECZNE</strong> - mog mie rn form, od
              krtkich solowych wystpw, poprzez tace w wykonaniu kilku
              tancerzy po ciekawe grupowe choreografie z dopasowanymi do
              okolicznoci barwnymi strojami. Mog by zarwno do muzyki
              odtwarzanej jak rwnie granej na ywo. Wsppracujemy z wieloma
              zespoami muzyki irlandzkiej, midzy innymi z grupami Duan, Ceili
              oraz Patricks Hat. Z zespoem Taorluath sworzylimy nasz
              najnowszy projektFeel the Reel.
            </li>
            <li>
              <strong>NAUKA PROSTYCH TACW</strong>  oprcz wystpw moemy
              zaproponowa wspln zabaw z publicznoci, podczas ktrej uczymy
              podstawowych krokw taca irlandzkiego. Jest to wesoa integrujca
              zabawa, bardzo lubiana przez wszystkich.
            </li>
            <li>
              <strong>WARSZTATY TANECZNE</strong> moemy prowadzi
              profesjonalne warsztaty taneczne na rnych poziomach
              zaawansowania. wietnie sprawdzaj si na festiwalach
              artystycznych, czy jako tematyczne zajcia szkolne lub program
              aktywnej integracji pracownikw.
            </li>
          </ul>
        </div>
        <div className="page-info-images">
          <img src={img1} alt="" />
          <img src={img2} alt="" />
        </div>
      </div>
      <h2>Ceny</h2>
      <div className="page-info-flex-container">
        <div className="page-info-text">
          <p>
            Cenawystpw jest zalena od wybranego repertuaru. Cena jest
            zwizana z dugoci pokazu i iloci zaangaowanych w nim tancerzy.
            Minimalnie pokaz moe trwa kilka minut przy udziale 1-2 tancerzy.
            Maksymalnie w pgodzinny pokaz taca zaangaowany jest cay zesp.
            Najbogatsze w naszej ofercie SHOWFeel the reel, bdce pokazem do
            muzyki na ywo, moe trwa do 1.5 godziny i bierze w nim udzia
            peen skad Treblers.
          </p>
          <p>
            Prosimy okontakt mailowy lub telefonicznyaby ustali szczegy
            oferty.
          </p>
        </div>
        <div className="page-info-images">
          <img src={img3} alt="" />
        </div>
      </div>

      <h2>Wymagania sceniczne</h2>

      <ul className="offer-requirements">
        <li>
          Scenalub stabilna, rwna powierzchnia o odpowiedniej nawierzchni do
          taca, najlepiej podoga drewniana lub wykadzina baletowa (trawa,
          posadzki kamienne i betonowe s wykluczone ze wzgldw bezpieczestwa
          tancerzy)
        </li>
        <li>odpowiednie nagonienieoraz sprzt z odtwarzaczem CD lub MP3</li>
        <li>garderoba zlokalizowana moliwie blisko sceny.</li>
      </ul>
    </div>
  );
};
